.menu {
  box-sizing: border-box;

  height: 100vh;
  background-color: $back-menu;
  font-weight: 700;
  text-transform: uppercase;

  &__top {
    margin-bottom: 32px;

    @include onTablet {
      margin-bottom: 0;
    }
  }

  &__bottom {
    margin-top: 32px;

     @include onTablet {
      margin-top: 56px;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone__number {
    display: block;
    margin-bottom: 16px;

    font-size: 16px;
    line-height: 27px;
    color: inherit;
    text-decoration: none;
  }

  &__call-to-order {
    display: block;
    width: fit-content;

    padding-bottom: 7px;
    border-bottom: 1px solid $main-text-color;

    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    color: inherit;
    text-decoration: none;
  }
}
